.map {
  display: inline-flex;
  width: 72vw;
  position: absolute;
  left: 35vw;
  height: 80%;
  flex-direction: column;
  justify-content: center;
}

.map svg {
  overflow: visible;
}

.map path {
  transition: fill 0.3s ease-in-out;
}

.cityViewTooltip {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 0.5em;
  background-color: #fdfdfd;
  font-weight: bold;
  padding: 0.5em;
  white-space: nowrap;

}