

.menu {
  width: 25vw;
  display: inline-flex;
  height: 85%;
  padding-left: 3vw;
  position: absolute;
  top: 0;
  flex-direction: column;
  justify-content: center;
}

.menu img {
  margin-bottom: 0.5em;
}

.text {
  text-align: center;
}

.title, .instructions {
  font-weight: bold;
}

.title {
  font-size: 150%;
  margin-bottom: 0.5em;
}

.subtitle {
  padding: 0em 0 1em;
}

.instructions {
  margin-bottom: 1em;
  font-size: 80%;
  white-space: nowrap;
}

.menu ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.menu ul li {
  background-color: #fff;
  margin: 0.2em 2em;
  padding: 0.5em;
  border-left: 0.25em #d4dbe5 solid;
  cursor: pointer;
  transition: border-color 0.3s ease-in-out;
  user-select: none;
}