.CityViewQuestions {
  position: absolute;
  bottom: 10vh;
  left: 40vw;
  width: 60vw;
  overflow: hidden;
  pointer-events: none;
}


.CityViewQuestions .question {
  color: #fff;
  margin-bottom: 0.2em;
  padding: 0.5em;
  font-weight: bold;
}

.close {
  text-align: right;
  font-size: 200%;
  transition: opacity 0.3s ease-in-out;
  padding-right: 0.5em;
  pointer-events: none;
}
.close span {
  cursor: pointer;
  pointer-events: all;
}