.AutoplayQuestionTooltip {
  position: absolute;
  top: 0;
  left: 0;

  max-width: 20vw;
}


.cityName {
  background-color: #dfdfdf;
  border-top-left-radius: 0.5em;
  border-top-right-radius: 0.5em;
  font-weight: bold;
  padding: 0.5em 1em;
}
.AutoplayQuestionTooltip .question {
  background-color: #fdfdfd;
  border-bottom-left-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  font-weight: bold;
  padding: 0.5em 1em;
  font-size: 80%;
}
